import React from 'react';
import { BEMHelper } from '../../../utils/bem-helper';
import "./Cards.scss";
import {Avatar} from "antd"
import { DeleteOutlined } from '@ant-design/icons';

const classHelper = BEMHelper("card");
const sourceImage = "data:image/jpeg;base64,"


export const Card = ({data, isDelete,handleClick, url}) => (
    <div className={classHelper("")} onClick={handleClick}>
            <div className={classHelper("title")}>
                <span>
                <Avatar shape="square" size={32} src={url? url: sourceImage}/>
                 </span>
                <span className={classHelper("heading")}>{data.title && data.title.toUpperCase()}</span>
               {isDelete && <span className={classHelper("delete")}><DeleteOutlined /></span>}
            </div>
    </div>
)